import React from 'react';
import * as styles from './LoadingIndicator.scss';

const LoadingIndicator = () => (
  <div className={styles.liWrapper}>
    <div className={styles.liBg} />
    <div className={styles.li} />
  </div>
);

export default LoadingIndicator;



