// @flow
import React, { Component } from 'react';
import LoadingIndicator from './LoadingIndicator';

export default function asyncComponent(getComponent: Function, props?: AsyncComponentProps) {
  return class AsyncComponent extends Component<AsyncComponentProps, AsyncComponentState> {
    static component = null;

    state = {
      loaded: false,
    };

    componentWillMount() {
      if (this.state.loaded) return;
      getComponent()
        .then(module => {
          AsyncComponent.component = module.default;
          this.setState({ loaded: true });
        });
    }

    render() {
      if (this.state.loaded && AsyncComponent.component) {
        return <AsyncComponent.component {...props} />;
      }
      return <LoadingIndicator />;
    }
  };
}



